.checkout_wrap{
    max-width:600px;
    margin: 0px 20px;
    margin-top: 30px;
}
.checkout_form{
    display: grid;
    grid-template-columns: auto auto;
    

}
.checkout_coloumn{
    margin-bottom: 20px;
    margin-right: 20px;

}

.checkout_coloumn label{
    display: block;
}
.checkout_coloumn input{
    width: 100%;
}
.checkout_coloumn select{
    width: 100%;
    border-left:0;
    border-right: 0;
    border-top: 0;
    padding: 10px 0px;
}
.checkout_coloumn button{
    background-color: #f0c14b;
    border-radius: 5px;
    width: 100%;
    border: 0;
    outline: 0;
    cursor: pointer;
    padding: 10px 0px;
}
.order_confirm{
    width: 600px;
    margin: 20px auto;
    text-align: center;
    background-color: whitesmoke;
    padding: 20px;
}
.order_confirm h3{
    background: black;
    color: white;
    margin: 10px 0px;
    padding: 10px;
}
.order_confirm button{
    background-color: #f0c14b;
    border-radius: 5px;
    width: 50%;
    font-size: 18px;
    border: 0;
    outline: 0;
    cursor: pointer;
    padding: 10px 0px;
    margin-top: 20px;
}